<template>
  <div class="warpp" ref="warpp">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";

export default {
  props: {
    probetype: {
      type: Number,
      default: 0
    },
    pullupload: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scroll: null
    };
  },
  mounted() {
    //1.BScrill 
    this.scroll = new BScroll(this.$refs.warpp, {
      click: true,
      probeType: this.probetype,
      pullUpLoad: this.pullupload
    });

    //
    if (this.probetype === 2 || this.probetype === 3) {
      this.scroll.on("scroll", position => {
        // 
        this.$emit("slide", position);
      });
    }

    //
    if (this.pullupload) {
      this.scroll.on("pullingUp", () => {
        // 
        this.$emit("pullingup");
      });
    }
  },
  methods: {
    //backtop 
    scrolltop(x, y, time = 1500) {
      this.scroll && this.scroll.scrollTo(x, y, time);
      this.scroll.refresh();
    },
    //
    finishpullup() {
      this.scroll && this.scroll.finishPullUp();
    },
    //   this.scroll &&  
    refresh() {
      this.scroll && this.scroll.refresh();
      // 
    }
  }
};
</script>